	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>the supply management system was established in the early 1970s to counter price instability, supply uncertainty and fluctuations in producer revenues;</p>\n</li>\n<li>\n<p>supply management is a model that benefits citizens, governments, society, agri-food industry and producers;</p>\n</li>\n<li>\n<p>the Liberal Party of Canada supports the supply management system and its three pillars; production planning based on Canadian consumer demand, negotiated cost of production producer prices, and import controls to effectively adjust supply and demand;</p>\n</li>\n<li>\n<p>the Canadian Dairy Commission is a Crown corporation established in 1966 with a mandate to coordinate federal and provincial dairy policies and create a dairy production control mechanism to stabilize revenues and avoid costly surplus production; </p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal Party of Canada establish a working group to develop solutions to protect the supply management system and to modernize the Canadian Dairy Commission. </p>\n<p><strong>BE IT FURTHER RESOLVED</strong> the recommendations of this task force will be sent to the Right Honourable PrimeMinister, his Cabinet and all Members of Parliament to serve as a strategic vision for the Government of Canada to promote the supply management system. (Quebec)</p>","frontmatter":{"path":"/policy/protection-of-the-supply-management-system-and-modernize-the-canadian-dairy-commission","date":"Mon Mar 05 2018 23:27:04 GMT-0500 (EST)","title":"Protection of the Supply Management System and Modernize the Canadian Dairy Commission"}}},"pathContext":{}}

